import React, { Component } from 'react';
import {  base } from 'nc-lightapp-front';
import './index.less'
const {NCForm,NCInput,NCTable} = base;

class DeptTreeVersion extends Component {
    getMemo(event){
        this.props.config.getMemo(event.target.value);
    }
    render(){
        return (
            <div className='branch'>
                <table>
                    <tr>
                        <td width="9%"></td>
                        <td>
                            {this.props.config.json['10100DEPT-000071']/* 国际化处理： 当前版本号*/}
                        </td>
                        <td>
                            <input name='vno' className='not-allow' type='text' style={{width:'200px',height:'30px'}} disabled="disabled" value={this.props.config.vno}/>
                        </td>
                        <td width="9%"></td>
                        <td>
                            {this.props.config.json['10100DEPT-000072']/* 国际化处理： 当前版本开始时间*/}
                        </td>
                        <td>
                            <input name='vbegindate' className='not-allow' type='text' style={{width:'200px',height:'30px'}} disabled="disabled" value={this.props.config.vbegindate}/>
                        </td>
                        <td width="9%"></td>
                    </tr>
                    <tr>
                        <td width="9%"></td>
                        <td>
                            {this.props.config.json['10100DEPT-000073']/* 国际化处理： 新版本说明*/}
                        </td>
                        <td>
                            <input name='vname' type='text' autofocus='autofocus' style={{width:'200px',height:'30px'}} onBlur={this.getMemo.bind(this)}/>
                        </td>
                        <td width="9%"></td>
                        <td>
                            {this.props.config.json['10100DEPT-000074']/* 国际化处理： 新版本生效日期*/}
                        </td>
                        <td>
                            <input name='veffectdate' className='not-allow' style={{width:'200px',height:'30px'}} type='text' disabled="disabled" value={this.props.config.veffectdate}/>
                        </td>
                        <td width="9%"></td>
                    </tr>
                </table>
            </div>

        )
    }

}

export default DeptTreeVersion;
